<template>
  <div>
    <!--   -->
    <nav class="navbar bg-body-tertiary fixed-top">
      <div class="container-fluid">
        <div class="navbar_logo">
          <!--    -->
          <span class="MatchingUs">
            <img  @click="goToMainPage" src="/image/logo.png" class="img"  alt=" " />
          </span>
        </div>
        <div class="navbar_right">
          <div class="navbar_welcome">
            <!--     -->
            <div class="welcome">, <strong class="name">{{ user.name }}</strong>! <i class="fa-regular fa-face-smile"></i></div>
          </div>
          <!--   -->
          <button @click="goToLoginPage" class="btn btn-outline-success" style="color: #9370DB; text-decoration: none;">
            <i  class="fa-solid fa-right-from-bracket"></i> LOG OUT!
          </button>
        </div>
      </div>
    </nav>

    <!--    -->
    <nav class="menuBar fixed-top">
      <div class="side_nav">
        <ul class="basicMenu">
          <li>
            <!--      -->
            <button @click="goToMyInfoPage"><p style="text-decoration: none; color: white;"> </p></button>
          </li>
          <li>
            <!--      -->
            <button @click="goToMatchingApplyPage"><p style="text-decoration: none; color: white;"> </p></button>
          </li>
          <li>
            <!--      -->
            <button @click="goToMatchingStatusPage"><p style="text-decoration: none; color: white;"> </p></button>
          </li>
          <li>
                    <!--    -->
                    <button @click="goToNoticePage"><p style="text-decoration: none; color: white;"></p></button>
                </li>
        </ul>
      </div>
    </nav>
    </div>
    
    <div class="content">
      <section id="postDetailContainer">
        <div class="post-it" :class="{ male: postDetail.gender === 'male', female: postDetail.gender === 'female' }">
    <h3>{{ postDetail.matchingTitle }}</h3>
    <p>: {{ postDetail.matchingType }}</p>
    <p>: {{ postDetail.department }}</p>
    <p>: {{ postDetail.matchingContent }}</p>
</div>
          <div class="comments-section">
        <!-- <h4></h4> -->
        <div class="comments-list">
          <ul class="comment-ul">
  <li v-for="comment in comments" :key="comment.commentID">
    {{ comment.content }}
  </li>
</ul>
        </div>

        <div class="add-comment" >
        <textarea class="textarea1" v-model="content" placeholder=" ."></textarea>
        <button class="comment-button" @click="addComment"> </button>
    </div>

        <p class="warning-text" v-if="isCommentEmpty">  .</p>
      </div>
    </section>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
  return {
    comments: [],
    postDetail: {},
    content: '',
    isCommentEmpty: false
  };
},
created() {
    const postID = this.$route.params.postID;
    axios.get(`http://localhost:3001/api/getPostDetail/${postID}`)
    .then(response => {
        this.postDetail = response.data;
    })
    .catch(error => {
        console.error('Error fetching post details:', error);
    });
    axios.get(`http://localhost:3001/api/getComments/${postID}`)
.then(response => {
    this.comments = response.data;
})
.catch(error => {
    console.error('Error fetching comments:', error);
});
},
computed: {
    user() {
      return JSON.parse(localStorage.getItem('user')) || {};
    }
  },  
  methods: {
    async addComment() {
            const postID = this.postDetail.postID;
            const loggedInUser = JSON.parse(localStorage.getItem('user'));
            const userID = loggedInUser.id;

            try {
                const response = await axios.post('http://localhost:3001/api/addComment', {
                    postID,
                    userID,
                    content: this.content //  
                });

                if (response.status === 201) {
                    alert(' !');
                    this.content = ''; //   
                    this.$router.go(0);
                }
            } catch (error) {
                console.error('    :', error);
                alert('  !');
                this.$router.go(0);
            }
        },

    
    //    
    goToMyInfoPage() {
      this.$router.push("/MyInfoPage");
    },

    goToLoginPage() {
      this.$router.push("/");
    },

    //    
    goToMatchingApplyPage() {
      this.$router.push("/MatchingApplyPage");
    },

    //    
    goToMatchingStatusPage() {
      this.$router.push("/MatchingApplyDetailPage");
    },

    goToMainPage() {
      this.$router.push("/MainPage");
    },

    goToNoticePage() {
      this.$router.push("/NoticePage");
    }
  }
}
</script>

