        formFields[i].disabled = true;
      } else {
        formFields[i].disabled = !this.editMode;
      }
    }
  }
},


saveOriginalFormData() {
  const formFields = this.$refs.myForm.elements;
  for (let i = 0; i < formFields.length; i++) {
