    if (
      formFields[i].id !== 'studentID' &&
      formFields[i].id !== 'name' &&
      formFields[i].type !== 'radio' &&
      formFields[i].id !== 'age'
    ) {
      this.originalFormData[formFields[i].id] = formFields[i].value;
    }
  }
},

    //        .
