  </button>
  <button v-if="editMode" type="button" class="edit-btn edit-btn-purple" @click="cancelChanges">
    
  </button>
  <button v-else type="button" class="edit-btn edit-btn-purple" @click="enableEditMode">
    
  </button>
</div>
<p class="result-message" :style="{ color: resultMessageColor }">{{ resultMessage }}</p>


