  <script>
  import axios from 'axios';

export default {
  data() {
    return {
      editMode: false, // Set to false initially
      matchingType: '',
      matchingTitle: '',
      matchingContent: '',
      resultMessage: '',
      resultMessageColor: '',
      postDetail: {},
      // ... (other data properties)
    };
  },
  created() {
    const postID = this.$route.params.postID;
    axios.get(`http://localhost:3001/api/getPostDetail/${postID}`)
    .then(response => {
        this.postDetail = response.data;
    })
    .catch(error => {
        console.error('Error fetching post details:', error);
    });
},
  computed: {
      user() {
        return JSON.parse(localStorage.getItem('user')) || {};
      }
    },  
    methods: {
      updateUserInformation() {
  const postID = this.$route.params.postID;
  const updatedData = {
    matchingType: this.postDetail.matchingType,
    matchingTitle: this.postDetail.matchingTitle,
    matchingContent: this.postDetail.matchingContent,
  };

  axios
    .put(`http://localhost:3001/api/updatePost/${postID}`, updatedData)
    .then((response) => {
      alert(' !', response.data);
      this.$router.go(0);
      this.editMode = false;
    })
    .catch((error) => {
      console.error('  :', error);
    });
},

    enableEditMode() {
      this.editMode = true; // Activate edit mode
    },
    cancelChanges() {
      this.editMode = false; // Deactivate edit mode
      // ... (other cancel logic)
    },
      async applyMatching() {
        //     
        if (
            this.matchingType !== '' &&
            this.matchingTitle.trim() !== '' &&
            this.matchingContent.trim() !== ''
        ) {
            
  const user = JSON.parse(localStorage.getItem('user'));
  const userID = user ? user.id : null;
  
  const postData = {
                matchingType: this.matchingType,
                matchingTitle: this.matchingTitle,
                matchingContent: this.matchingContent,
                userID: userID  // You might also need to send the userID
            };
  
            // Send the post data to the server
            try {
              const response = await fetch("http://localhost:3001/api/addPost", {
                method: 'POST',
                headers: { 
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(postData)
            });
            
            if (response.ok) {
              const data = await response.json();
              alert(data.message);
              this.resultMessage = '!';
              this.resultMessageColor = 'green';
              this.$refs.myForm.reset();
              } else {
                const errorData = await response.json();
                alert(errorData.error);
                this.resultMessage = '  .';
                this.resultMessageColor = 'red';
                }
            } catch (error){
              console.error("", error);
            }
        }
      },
  
      cancelMatching() {
        //      
        this.matchingType = '';
        this.matchingTitle = '';
        this.matchingContent = '';
        this.resultMessage = '';
        this.resultMessageColor = ''; //  
      },
  
      //    
      goToMyInfoPage() {
        this.$router.push("/MyInfoPage");
      },
  
      //    
      goToMatchingApplyPage() {
        this.$router.push("/MatchingApplyPage");
      },
  
      //    
      goToMatchingApplyDetailPage() {
        this.$router.push("/MatchingApplyDetailPage");
      },
  
      //   
      goToLoginPage() {
        this.$router.push("/");
      },
  
      goToMainPage() {
        this.$router.push("/MainPage");
      },
  
      goToNoticePage() {
        this.$router.push("/NoticePage");
      },
    }
  }
  </script>
  
